.word-wrap{
  display: flex;
  flex-direction: column;
  width: 100%;
}

.word-top{
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 480px;
  
}

.word-input{
  width: 50%;
  display: flex;
  flex-direction: column;
  
}
.word-input>div:nth-child(1){
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: row;
}
.word-input>div:nth-child(2){
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: row;
}
.word-input>div:nth-child(3){
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: row;
}

.word-english-input>div:nth-child(1),.word-korean-input>div:nth-child(1){
  width: 15%;
  height: 50px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}
.word-english-input>input:nth-child(2),.word-korean-input>input:nth-child(2){
  width: 50%;
  height: 50px;
  box-sizing: border-box;
  padding: 0px 20px;
  font-size: 1.3rem;

}

.word-button-zone>div:nth-child(1){
  width: 10%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
  margin-left: 70px;
}
.word-button-zone>div:nth-child(2){
  width: 10%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
  margin-left: 30px;
}
.word-button-zone>div:nth-child(3){
  width: 10%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: rgb(255, 255, 255);
  margin-left: 30px;
  font-size: 1rem;
}
.word-button-zone>div:nth-child(4){
  width: 15%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: rgb(255, 255, 255);
  margin-left: 30px;
  font-size: 1rem;
}
.word-button-zone>div:nth-child(5){
  width: 10%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: rgb(255, 255, 255);
  margin-left: 30px;
  font-size: 1rem;
}
.word-button-zone>div:hover {
  background-color: gray;
  transition: background-color 0.4s ease; /* 변경된 부분 */
}
.word-button-zone>div:hover{
  cursor: pointer;
}


.word-view{
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 100%;
}
.word-view-itemWrap{
  width: 100%;
  height: 80%;
  overflow-y: auto; /* 수직 방향으로 스크롤 생성 */
  overflow-x: hidden; /* 가로 방향 스크롤을 숨김 */
}
.word-view-item{
  width: 100%;
  height: 40px;
  display: flex;
  flex-direction: row;
  margin: 3px 0;
}
.word-view-item>div{
  display: flex;
  justify-content: center;
  align-items: center;  
}
.word-view-item>div:nth-child(1){
  width: 10%;
}
.word-view-item>div:nth-child(2){
  width: 25%;
  border: 1px solid black;
}
.word-view-item>div:nth-child(3){
  width: 25%;
  border: 1px solid black;
  margin: 0px 10px;
}
.word-view-item>div:nth-child(4){
  width: 120px;
  height: 80%;
  background-color: black;
  color: white;
  margin-left: 30px;
}

.word-view-toggle{
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  border: 1px solid black;
  box-sizing: border-box;
}




.word-bottom{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 200px;
  margin-top: 80px;
  
}
.word-english-zone{
  display: flex;
  flex-direction: row;
  height: 50%;
  justify-content: center;
  align-items: center;
}
.word-korean-zone{
  display: flex;
  flex-direction: row;
  height: 50%;
  justify-content: center;
}
.word-korean-zone>div,.word-english-zone>div{
  display: flex;
  justify-content: center;
  align-items: center;
}

.word-english-zone>div:nth-child(1),.word-korean-zone>div:nth-child(1){
  width: 20%;
  height: 100%;
  font-size: 1.8rem;
}
.word-english-zone>div:nth-child(2),.word-korean-zone>div:nth-child(2){
  width: 20%;
  height: 100%;
}
.word-english-zone>div:nth-child(2)>input,.word-korean-zone>div:nth-child(2)>input{
  width: 100%;
  height: 50px;
  box-sizing: border-box;
  padding-left: 30px;
  font-size: 1.8rem;
}
.word-english-zone>div:nth-child(3),.word-korean-zone>div:nth-child(3){
  width: 20%;
  height: 100%;
  font-size: 1.8rem;
}

.voiceButton{
  height: 60px;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: pink;
}

.examples{
  
}

.example{
  height: 60px;
  font-size: 2rem;
  padding-left: 100px;
}

.file-name{
  display: flex;
  flex-direction: row;
}

.file-name>div{
  font-size: 0.9rem;
  margin: 10px;
  color: gray;
  transition-duration: 0.7s;
}
.file-name>div:hover{
  color: purple;
  scale: 1.6;

}