/* 해더, 푸터, 메인 */
.header{
  width: 100%;
  height: 150px;
  background-color: beige;
  box-sizing: border-box;
}
.header{
  font-size: 2rem;
  padding: 20px;
}
.header:hover{
  color: blue;
  cursor: pointer;
}

.footer{
  width: 100%;
  height: 150px;
  background-color: aliceblue;
}

*{
  margin: 0px;
  padding: 0px;
}

.app{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content{
  flex: 1;
}
/* 해더, 푸터, 메인 */

.hyper-link:hover{
  text-decoration: underline;
  color:blue;
  cursor: pointer;
}
.hyper-link{
  margin: 20px 0px;
}

.content{
  width: 90%;
  margin: 50px auto;
}

