/* 큰틀 */
.roulette-wrap{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 1000px;
}

/* 위 */
.roulette-top{
  display: flex;
  flex-direction: row;
  width: 100%;
}
.roulette-top>div{
  width: 50%;
  display: flex;
}
.roulette-input{

}


.roulette-inputArea{
  display: flex;
  flex-direction: column;
  width: 100%;
}
.roulette-inputArea>div{
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 50px;
  align-items: center;
  margin-bottom: 30px;
}
.roulette-inputArea>div>:nth-child(1){
  width: 10%;
}
/* 인풋 */
.roulette-inputArea>div>:nth-child(2){ 
  width: 50%;
  height: 50px;
  box-sizing: border-box;
  padding-left: 30px;
  font-size: 1.4rem;
}
.roulette-inputArea>div>:nth-child(3){
  border: 1px solid black;
  background-color: black;
  width: 10%;
  height: 40px;
  margin-left: 30px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.roulette-inputArea>div>:nth-child(3):hover{
  cursor: pointer;
}
.roulette-inputArea>div>:nth-child(4){
  border: 1px solid black;
  background-color: black;
  width: 10%;
  height: 40px;
  margin-left: 30px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.roulette-inputArea>div>:nth-child(4):hover{
  cursor: pointer;
}

.roulette-inputArrayView{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
}
.roulette-arrayItem{
  border: 3px solid black;
  height: 40px;
  min-width: 100px;
  margin: 10px 5px;
  display: flex;

  justify-content: center;
  align-items: center;
}
.roulette-arrayItem:hover{
  cursor: pointer;
  background-color: rgb(144, 143, 143);
  transition: background-color 0.3s ease;
}

/* 아래 */
.roulette-bottom{
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 100px 0;
  justify-content: center;
}

.roulette-bottom>div:nth-child(1){
  border: 1px solid blue;
  background-color: blue;
  color: white;
  width: 120px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.roulette-bottom>div:nth-child(1):hover{
  cursor: pointer;
}
.roulette-bottom>div:nth-child(2){
  border: 1px solid black;
  margin: 0px 50px;
  min-width: 100px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 20px;
}
